* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.services,
.products,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.services {
 background-image: url('images/img-2.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.products {
  background-image: url('images/img-2.jpg');
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.sign-up {
  background-image: url('images/img-2.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}
.sign-up .form-1{
  align-items: center;
  justify-content: center;
  border:solid black 1rem;

}
.sign-up .form-1 .na,
.sign-up .form-1 .ad
{
  font-size: 3rem;
  color:black;
  display: flex;
  flex-direction: row;

}
.sign-up .form-1 .na .name,
.sign-up .form-1 .ad .address{
background: transparent;
border-color: transparent;

}
 .Signup {
    border: 1px solid #ddd;
    padding: 0px 20px;
    margin: 20px 0px;
    box-shadow: 10px 10px 5px grey;
}
.Signup a {
    color: #9c27b0;
}
 .btn {
    width: 100%;
    margin: 10px 0px;
    font-size: 18px;
    background-color: #9c27b0;
    border: none;
}
.form-group{
  font-size: medium;
}
.Signup h3 {
  margin: 20px 0px 30px;
  padding: 0px 0px 25px;
  text-align: center;
  border-bottom: 1px solid #ddd;
   color: #9c27b0;
}